import { Component, OnInit } from '@angular/core';
import { HttpTestService } from '../http-test.service';
import { menus } from '../../app/components/menus';
import { Router } from '@angular/router';
import { constants } from 'src/constants/constants';
import { MenuController, ModalController } from '@ionic/angular';
import { AboutComponent } from '../modals/about/about.component';

@Component({
  selector: 'app-support-list',
  templateUrl: './support-list.page.html',
  styleUrls: ['./support-list.page.scss'],
})
export class SupportListPage implements OnInit {

  navigate:[];
  toDoList:[];
  header1: string;
  header2: string;
  header3: string;
  image_url: string;
  base_url: string = `${constants.baseUrlProd}`;
  list_exists:boolean = false;


  constructor(private service: HttpTestService, private  router:  Router, private menu: MenuController,private modalCtrl: ModalController) { }

  ngOnInit() {
    
  }
  close(){
    this.menu.close();
  }
  ionViewWillEnter(){
    
    this.navigate = menus.support;
    this.header1 = window.sessionStorage.getItem('uid');
    this.header2 = window.sessionStorage.getItem('access-token');
    this.header3 = window.sessionStorage.getItem('client');
    console.log("ESSES SÃO OS HEADERS!"+ this.header1, this.header2, this.header3)
    this.service.getUserData('api/v1/get-meals', this.header1, this.header2, this.header3).subscribe(resp =>{
      console.log(JSON.stringify(resp.body))
      
      
    this.toDoList = (resp.body != undefined && resp.body != null) ? resp.body : [];
    this.list_exists = (this.toDoList.length == 0) ? false : true ;
    });
  }

  changePage(id_meal, meal_type, item_description, image_file_name, created_at,user_image_url,user_name){
    sessionStorage.setItem('id_meal', id_meal)
    sessionStorage.setItem('meal_type',meal_type)
    sessionStorage.setItem('description', (item_description))
    sessionStorage.setItem("image_file_name",image_file_name)
    sessionStorage.setItem("meal_created_at",created_at)
    sessionStorage.setItem("user_image_url",user_image_url)
    sessionStorage.setItem("user_name",user_name)
    this.router.navigateByUrl('support-feedback');
  }

  async about(){
    
    const modal = await this.modalCtrl.create({
      component: AboutComponent,
      cssClass:'login_page_about_modal'
    });
    modal.onDidDismiss().then(() => {
      this.close();
      this.ionViewWillEnter()
      
    });
    return await modal.present();
    
  }

  logout(){
    this.service.signOut('api/auth/sign_out', this.header1, this.header2, this.header3).subscribe(resp =>{
     if(resp.status === 200){
      this.router.navigateByUrl('login');
     }else{
       confirm("Erro ao sair. Tente novamente mais tarde.")
     }
    });  

  }

}
